import { Component, Prop, Event, EventEmitter, h, State } from '@stencil/core';
import { iFormField } from '../../models/form-field.model';

@Component({
  tag: 'select-field',
  styleUrl: 'select-field.css',
  shadow: false,
  scoped: false,
})
export class SelectField {
  @Prop() field: iFormField;
  @Prop() isInvalid: boolean;
  @Prop() isHidden: boolean;
  @Prop() isDisabled: boolean;
  @Event({ bubbles: true, composed: true }) valueChanged: EventEmitter<{ key: string; value: any }>;
  @Event({ bubbles: true, composed: true }) fieldBlurred: EventEmitter<{ key: string; }>;

  @State() isTouched: boolean = false;

  private onValueChange(e: Event) {
    const value = (e.target as HTMLInputElement).value;
    this.valueChanged.emit({ key: this.field.key, value });
  }

  private handleBlur() {
    this.isTouched = true;
    this.fieldBlurred.emit({ key: this.field.key });
  }

  render() {
    if (this.isHidden) {
      console.log('Select field is hidden:', this.field.key, this.field.value);
      return null;
    }

    return (
      <section>
        <select
          id={this.field.key}
          class={`${this.isInvalid ? 'invalid' : ''} ${this.isTouched ? 'touched' : 'untouched'}`}
          disabled={this.isDisabled}
          onInput={(e) => this.onValueChange(e)}
          onBlur={() => this.handleBlur()}>

          <option value="" selected={!this.field.value}>
            {this.field.placeholder || 'Select an option'}
          </option>

          {this.field.options?.map(option => {
            const optionValue = option[this.field.optionValue];
            return (
              <option value={optionValue} selected={optionValue === this.field.value}>
                {option[this.field.optionLabel]}
              </option>
            );
          })}
        </select>

        {this.field.label && (
          <label
            htmlFor={this.field.key}
            innerHTML={this.field.label.replace(/\[\--(.*?)\--\]/g, ' <span class="file-size-limit">($1)</span>')}>
          </label>
        )}

        <slot></slot>
      </section>
    );
  }
}
